@tailwind base;
@tailwind components;
@tailwind utilities;

.header-links{
    @apply flex flex-1 flex-wrap place-items-center overflow-hidden;
    @apply text-2xl font-semibold ml-8 text-lightest_slate;
    @apply fixed place-items-center w-screen p-6 z-0;
    
}
.header-links-right{
    @apply ml-auto mr-8 flex gap-5 items-center z-0;
}
.header-hover{
    @apply hover:text-primary;
}

.header-hamburger{
    @apply cursor-pointer z-10;
}

